      font-size: 1.125rem;
  }
}

.wpo-blog-single-section .post blockquote {
  background-color: #f8f8f8;
  padding: 50px 30px 50px 95px;
  margin: 60px 0;
  border: 0;
  line-height: 1.9em;
  position: relative;
}

@media (max-width: 767px) {
