.wpo-blog-single-section .post blockquote:before {
  font-family: "Flaticon";
  content: "\f113";
  background:#00d690;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 35px;
  position: absolute;
  left: 30px;
  top: 40px;
}

@media (max-width: 991px) {
  .wpo-blog-single-section .post blockquote:before {
