}

.wpo-blog-single-section .tag-share .tag a {
  background-color: #f8f8f8;
  display: block;
  padding: 9px 18px;
  color: #525252;
  border-radius: 50px;
}

.wpo-blog-single-section .tag-share .tag a:hover {
  background-color: #00d690;
  color: #fff;
}

.wpo-blog-single-section .tag-share .share a {
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  font-size: 0.875rem;
