  color: #525252;
  border-radius: 50%;
  padding: 0;
  display: block;
  border: 1px solid #dadada;
}

.wpo-blog-single-section .tag-share .share a:hover {
  background: #00d690;
  color: #fff;
  border-color: #00d690;
}

.wpo-blog-single-section .author-box {
  padding: 45px 50px 35px;
  margin: 70px 0;
  border: 1px solid #e1e1e1;
}

@media (max-width: 991px) {
  .wpo-blog-single-section .author-box {
      padding: 35px 40px;
  }
}

@media (max-width: 767px) {
  .wpo-blog-single-section .author-box {
