      padding-left: 50px;
  }
}

@media screen and (min-width: 1200px) {
  .wpo-blog-single-fullwidth .wpo-blog-content {
      padding: 0;
  }
}

.entry-media img {
  width: 100%;
}

.author-btn a {
  padding: 15px 35px;
  background: #f8f8f8;
  display: inline-block;
  border-radius: 40px;
  color: #555555;
}

.author-btn a:hover {
  background-color: #00d690;
  color: #fff;
}

.wpo-blog-pg-section .format-video .video-holder a {
  background-color: #fff;
  -webkit-animation: spineer 2s infinite;
  animation: spineer 2s infinite;
}

.wpo-blog-pg-section .format-video .video-holder a:before {
  border-left: 20px solid #111;
}
.wpo-blog-sidebar-2{
  margin-top: 0px;
  margin-bottom: 60px;
}
.wpo-blog-sidebar blockquote{
  background-color: #f8f8f8;
  padding: 50px 30px 50px 95px;
  margin: 60px 0 30px;
  border: 0;
  line-height: 1.9em;
  position: relative;
}
.wpo-blog-sidebar blockquote:before {
  font-family: "Flaticon";
  content: "\f119";
  font-size: 35px;
  position: absolute;
  left: 30px;
  top: 40px;
}
.quote-img img{
  border-radius: 50%;
}
.quote-img{
  position: relative;
  width: 96px;
  height: 96px;
  float: left;
  margin-right: 35px;
}
.quote-img:before{
  position: absolute;
  left: -9px;
  top: -9px;
  width: 120%;
  height: 120%;
  border:1px solid #d7d7d7;
  content: "";
  border-radius: 50%;
}
.quote-text{
  overflow: hidden;
}
.quote-text h2{
 font-size: 16px;
 color: #242f6c;
}
.quote-text span{
 font-size: 15px;
 color: #666666;
}
.quote-wrap {
  overflow: hidden;
  padding: 20px 10px 10px;
}

.blog-pg-left-sidebar .wpo-blog-sidebar{
 padding-left: 0;
 padding-right: 30px;
}
@media(max-width: 430px){
  .wpo-blog-single-section .comments-area ol ul {
      padding-left: 0;
      list-style: none;
  }
  .wpo-blog-sidebar blockquote {
      padding: 50px 30px 24px 70px;
      margin: 60px 0 30px;
  }
  .quote-img {
      float: none;
      margin-bottom: 20px;
  }
  .wpo-blog-single-section .tag-share .tag a {
      padding: 9px 16px;
  }
}




@-webkit-keyframes spineer {
  0% {
      -webkit-box-shadow: 0 0 0 0 #c7c1ea;
  }

  70% {
      -webkit-box-shadow: 0 0 0 20px rgba(1, 93, 199, 0);
  }

  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(1, 93, 199, 0);
  }
}

@keyframes spineer {
  0% {
      -webkit-box-shadow: 0 0 0 0 #c7c1ea;
      box-shadow: 0 0 0 0 #c7c1ea;
  }

  70% {
      -webkit-box-shadow: 0 0 0 20px rgba(1, 93, 199, 0);
      box-shadow: 0 0 0 20px rgba(1, 93, 199, 0);
  }

  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(1, 93, 199, 0);
      box-shadow: 0 0 0 0 rgba(1, 93, 199, 0);
  }
}

