.faq-area .MuiPaper-root {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(68, 68, 68, 0.1);
  box-shadow: 0px 5px 15px 0px rgba(68, 68, 68, 0.1);
  margin-bottom: 20px;
}

.faq-area .MuiPaper-root:before{
  display: none;
}

.faq-area .MuiAccordionSummary-content {
  margin: 25px 0;
}

.faq-area .MuiButtonBase-root{
  border: 0;
}



.choose-info .btn.btn-link {
  font-size: 1rem;
  font-weight: 600;
  position: relative;
  display: block;
  width: 100%;
  text-align: left;
  text-transform: capitalize;
  white-space: initial;
  border: none;
  background-color: #fff;
  color: #444;
  padding: 25px;
}

.choose-info .btn.btn-link::after {
  position: absolute;
  content: '\f107';
}

.choose-info .card {
  border: 0;
  border-radius: 0;
}

.choose-info .card .card-header {
  padding: 0;
  background-color: transparent;
}

.choose-info .card .card-header:first-child {
  border-radius: 0;

}

.choose-info .card:last-child .card-body,
.choose-info .card:last-child .card-header {
  border-bottom: 0;
}


.choose-info .btn.btn-link.collapsed::after,
.choose-info .btn.btn-link::after {
  font-family: "FontAwesome";
  font-weight: 900;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.choose-info .btn.btn-link.collapsed::after {
  content: '\f106';
}

.choose-info .btn.btn-link:hover,
.choose-info .btn.btn-link:focus {
  text-decoration: none;
  color: #00d690;
}