.theme-btn, .theme-btn-s2,.theme-btn-s4 {
  color: #00d690;
  font-weight: 600;
  padding: 15px 27px;
  border: 0;
  text-transform: capitalize;
  display: block;
  background: #fff;
  display: inline-block;
  position: relative;
  overflow: hidden;
}
.theme-btn:before,
.theme-btn-s2:before,
.theme-btn-s4:before{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    opacity: .3;
    z-index: 9;
    content: '';
    -webkit-transition: -webkit-transform 0.6s;
    transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    -webkit-transform: scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 180deg) translate3d(0, 100%, 0);
    transform: scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 180deg) translate3d(0, 100%, 0);
}

.theme-btn:hover:before,
.theme-btn-s2:hover:before,
.theme-btn-s4:hover:before{
    -webkit-transform: scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 180deg) translate3d(0, -100%, 0);
    transform: scale3d(1.9, 1.4, 1) rotate3d(0, 0, 1, 180deg) translate3d(0, -100%, 0);
}

.theme-btn:hover, .theme-btn-s2:hover, .theme-btn:focus, .theme-btn-s2:focus, .theme-btn:active, .theme-btn-s2:active {
  background-color: #00d690;
  color: #fff;
}
.theme-btn-s2 {
  background: #00d690;
  color: #fff;
}
    
@media (max-width: 991px) {
  .theme-btn, .theme-btn-s2 {
    font-size: 15px;
    font-size: 0.9375rem;
  }
}



