/*==============================================
#0.7 destination-area
================================================*/

.destination-area{
    background: #f5fbf9;
  }
  .destination-area.section-padding{
      padding-top: 230px;
  }
  .destination-area .container{
     width: 1425px;
     max-width: 100%;
  }
  .destination-text{
    padding-top: 100px;
  }
  .destination-text p{
    margin-bottom: 40px;
  }
  .destination-item{
    position: relative;
    margin-bottom: 30px;
  }
  .destination-content{
    background: #fff;
    padding: 20px;
    overflow: hidden;
    position: absolute;
    bottom: 20px;
    left: 50%;
    width: 90%;
    transform: translateX(-50%);
    border-radius: 10px;
    transition: all .3s;
  }
  .content-left{
    float: left;
  }
  .content-right{
    float: right;
  }
  .content-left h5{
    color: #283a5e;
    font-size: 22px;
    font-weight: 700;
    margin-top: 0;
  }

  .content-left h5 a{
    color: #283a5e;
  }
  .content-left h5 a:hover{
    color: #02cb89;
  }
  .content-left small{
    color: #687693;
    font-size: 12px;
  }
  .content-right h5{
    color: #02cb89;
    font-size: 25px;
    margin-top: 0;
  }
  .content-right p{
    color: #616f8c;
    font-size: 14px;
  }
  
  .destination-right{
    margin-top: -100px;
  }
  .destination-img img{
    width: 100%;
  }
  
  @media(max-width: 992px){
  .destination-area.section-padding {
      padding-top: 0;
  }
  .destination-right{
    margin-top: 0;
  }
  .destination-text {
      padding-top: 100px;
      padding-bottom: 50px;
  }
  }
  @media (min-width: 620px) and (max-width: 767px) {
    .custom-grid {
          width: 50%;
          float: left;
      }
  }

  .destination-service .destination-item{
   padding: 0 10px;
  }



  .destination-service .destination-item {
    transition: all 0.8s;
    -moz-transition: all 0.8s;
    -webkit-transition: all 0.8s;
    -ms-transition: all 0.8s;
    -o-transition: all 0.8s;
}
.destination-service .destination-item:hover {
    transform: translateY(-15px);
    -moz-transform: translateY(-15px);
    -webkit-transform: translateY(-15px);
    -ms-transform: translateY(-15px);
    -o-transform: translateY(-15px);
}

.destination-service .slick-list {
  overflow: inherit;
}

.destination-service{
  background: #f5fbf9;
}

@media(max-width:991px){
  .destination-service .slick-list {
    overflow: hidden;
  }
}


